<template>
  <div>
    <div style="margin-top: 10px">
      <div
        v-for="(item, index) in classificationList"
        :key="item"
        style="
          display: flex;
          align-items: flex-start;
          padding: 10px 0px;
          border-bottom: 1px solid #f3f3f3;
        "
      >
        <div
          style="
            width: 40px;
            height: 46px;
            position: relative;
            margin-right: 10px;
          "
        >
          <img
            style="position: absolute"
            :src="
              index + 1 === 1
                ? require(`@/assets/iamges/number1.png`)
                : index + 1 === 2
                ? require(`@/assets/iamges/number2.png`)
                : index + 1 === 3
                ? require(`@/assets/iamges/number3.png`)
                : require(`@/assets/iamges/numberall.png`)
            "
            alt=""
          />
          <div class="rank_num">
            {{ item.rank }}
          </div>
        </div>
        <div
          class="list_name"
          :style="{
            color:
              item.rank === 1
                ? '#F12828'
                : item.rank === 2
                ? '#FF7640'
                : item.rank === 3
                ? '#FDC45C'
                : '#333333',
          }"
        >
          {{ item.name }}
        </div>
        <div style="padding-top: 14px; flex: 1; margin: 0px 10px">
          <div
            style="height: 12px; flex: 1"
            :class="
              item.rank === 1
                ? 'tiao_one'
                : item.rank === 2
                ? 'tiao_two'
                : item.rank === 3
                ? 'tiao_three'
                : 'tiao_all'
            "
          ></div>
          <span
            :style="{
              color:
                item.rank === 1
                  ? '#F12828'
                  : item.rank === 2
                  ? '#FF7640'
                  : item.rank === 3
                  ? '#FDC45C'
                  : '#333333',
            }"
          >
            {{
              item.enterprise_comemnt_count
                ? item.enterprise_comemnt_count
                : item.total_comments
            }}
          </span>
        </div>
        <span class="list_btn" @click="goToComment(item.name)">评论</span>
      </div>
      <div style="margin: 10px 0px; height: 100px">
        <SeparatePage :pageCont="pageValue.value" :type="`搜索`" />
      </div>
    </div>
  </div>
</template>

<script setup>
import { fether } from "@/utils/tools";
import { reactive, ref, watch } from "vue";
import SeparatePage from "./SeparatePage.vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

const $store = new useStore();
const $router = new useRouter();

const props = defineProps({
  data: Object,
});

const pageValue = reactive({
  value: 1,
});

const getData = async (value) => {
  classificationList.splice(0, classificationList.length);
  let result = await fether(
    `/searchenterprise?search_value=${props.data.name}&page_num=${value}`
  );
  if (result.code === 200) {
    let card = 0;
    if (value) {
      card = value - 1;
    }
    result.data.map((item, index) => {
      classificationList.push({
        ...item,
        rank: 20 * card + index + 1,
      });
    });
  }
};

const classificationList = reactive([]);

const getList = () => {
  props.data.value.map((item, index) => {
    classificationList.push({ ...item, rank: index + 1 });
  });
  setTimeout(() => {
    pageValue.value = props.data.page_count;
  });
  $store.state.type = "搜索";
};
getList();

const goToComment = (value) => {
  $router.push({
    path: "/comment",
    query: {
      enterprise_name: value,
    },
  });
};

watch(
  () => $store.state.updataPage,
  (newVal) => {
    if ($store.state.type === "搜索") {
      getData(newVal);
    }
  }
);
</script>

<style scoped lang="scss">
#type_box_two {
  height: 40px;
  border: 1px solid #f3f3f3;
  border-radius: 0px 10px 10px 0px;
  overflow-x: scroll;
  white-space: nowrap;
  padding-top: 10px;
}
#type_box_two::-webkit-scrollbar {
  display: none;
}
.type_box_item_two {
  margin-left: 10px;
  display: inline-block;
  padding: 5px 10px;
  border-radius: 15px;
}
img {
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.rank_num {
  top: 10px;
  width: 100%;
  height: 80%;
  position: absolute;
  z-index: 2;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bolder;
  margin-right: 10px;
}
.list_btn {
  padding: 5px 10px;
  font-size: 12px;
  background: -webkit-linear-gradient(0deg, #ff2369, #ff6a3f);
  border-radius: 20px;
  color: white;
}
.tiao_one {
  background-image: linear-gradient(270deg, #f89494 0%, #f12828 100%);
}
.tiao_two {
  background-image: linear-gradient(270deg, #ffbba0 0%, #ff7640 100%);
}
.tiao_three {
  background-image: linear-gradient(270deg, #fee2ae 0%, #fdc45c 100%);
}
.tiao_all {
  background-image: linear-gradient(270deg, #ccc 0%, #999 100%);
}
.list_name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-top: 10px;
  width: 100px;
}
</style>
