// yyyy-mm-dd
export const getPartTime = (data) => {
  let value = new Date(data * 1000);
  let Y = value.getFullYear();
  let M =
    value.getMonth() + 1 > 10
      ? value.getMonth() + 1
      : `0${value.getMonth() + 1}`;
  let D = value.getDate() > 10 ? value.getDate() : `0${value.getDate()}`;
  return `${Y}-${M}-${D}`;
};
