<template>
  <div id="comment_body">
    <div style="height: 30px">
      <button style="width: 70px; height: 100%" @click="$router.push('/')">
        返回
      </button>
    </div>
    <div id="comment_box">
      <input
        type="text"
        ref="commentValue"
        style="padding-left: 15px"
        placeholder="请输入评论"
      />
      <button @click="sendComment">评论</button>
    </div>
    <div style="font-size: 20px; font-weight: bolder">评论列表</div>
    <div style="margin-top: 10px">
      <div v-for="item in commentList" :key="item" style="padding: 10px 0px">
        <div class="text_over">{{ item.content }}</div>
        <div style="font-size: 12px; margin-top: 10px">
          {{ getPartTime(item.create_time) }}
        </div>
      </div>
    </div>
    <div style="margin-top: 10px; padding-bottom: 10px">
      <SeparatePage :pageCont="pageValue" :type="'评论'" />
    </div>
  </div>
</template>

<script setup>
import { HOST } from "@/ENV";
import { fether } from "@/utils/tools";
import { reactive, ref, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import { useStore } from "vuex";
import { getPartTime } from "@/utils/getTime";
import SeparatePage from "@/views/compenmens/SeparatePage.vue";

const commentList = reactive([]);
const commentValue = ref("");
const $router = new useRouter();
const $route = new useRoute();
const $store = new useStore();

const pageValue = ref(1);

// 获取评论列表
const getCommentList = async (value, page) => {
  let result = undefined;
  if (page) {
    result = await fether(
      `/enterprisecomment?enterprise_name=${value}&page_num=${page}`
    );
  } else {
    result = await fether(`/enterprisecomment?enterprise_name=${value}`);
  }
  commentList.splice(0, commentList.length);
  if (result.code === 200) {
    $store.state.type = "评论";
    pageValue.value = result.page_count;
    result.data.map((item) => {
      if (item.status) {
        commentList.push({ ...item });
      }
    });
  }
};
getCommentList($route.query.enterprise_name);

const sendComment = async () => {
  if (commentValue._value.value === "") return;
  let result = await fetch(`${HOST}/enterprisecomment`, {
    method: "post",
    body: JSON.stringify({
      data: {
        content: commentValue._value.value,
        openid: "heart",
        enterprise_name: $route.query.enterprise_name,
      },
    }),
  })
    .then((res) => res.json())
    .then((data) => data);
  if (result.code === 200) {
    commentValue._value.value = "";
    // commentList.splice(0, commentList.length);
    // getCommentList($route.query.enterprise_name);
  }
};

watch(
  () => $store.state.updataPage,
  (newVal) => {
    if ($store.state.type === "评论") {
      getCommentList($route.query.enterprise_name, newVal);
    }
  }
);
</script>

<style scoped lang="scss">
#comment_body {
  max-width: 480px;
  padding: 10px;
  height: calc(100vh - 20px);
  overflow-y: scroll;
}
#comment_body::-webkit-scrollbar {
  display: none;
}
#comment_box {
  height: 30px;
  margin: 10px 0px;
  display: flex;
  justify-content: space-between;
  input {
    width: calc(100% - 80px);
  }
}
button,
input {
  outline: none;
  border: 1px solid #f3f3f3;
}
.text_over {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
</style>
