<template>
  <div id="list_scroll">
    <div id="list_rank">
      <img
        style="width: 50px; height: 104px"
        src="../../assets/iamges/dao_left.png"
        alt=""
      />
      <div style="width: calc(100% - 100px); color: #eed3a5">
        <div
          class="text_over"
          style="font-size: 52px; font-weight: bold; text-align: center"
        >
          {{ rankInfo.data.title }}
        </div>
        <div class="text_over label_info">
          {{ rankInfo.data.info }}
        </div>
      </div>
      <img
        style="width: 50px; height: 104px"
        src="../../assets/iamges/dao_right.png"
        alt=""
      />
    </div>
    <div id="rank_trophy" v-if="rankStatus.status">
      <div class="rank_trophy_four">
        <div style="line-height: 25px">
          {{ rankInfo.data.data[0].name }}
        </div>
        <div
          style="display: flex; align-items: center; justify-content: center"
        >
          <img
            width="10"
            height="13"
            src="../../assets/iamges/rankjiangpai.png"
            style="margin-right: 5px"
            alt=""
          />
          <span>{{ rankInfo.data.data[0].num }}</span>
        </div>
      </div>
      <div class="rank_trophy_five">
        <div style="line-height: 25px">
          {{ rankInfo.data.data[1].name }}
        </div>
        <div
          style="display: flex; align-items: center; justify-content: center"
        >
          <img
            width="10"
            height="13"
            src="../../assets/iamges/rankjiangpai.png"
            style="margin-right: 5px"
            alt=""
          />
          <span>{{ rankInfo.data.data[1].num }}</span>
        </div>
      </div>
      <div class="rank_trophy_six">
        <div style="line-height: 25px">
          {{ rankInfo.data.data[2].name }}
        </div>
        <div
          style="display: flex; align-items: center; justify-content: center"
        >
          <img
            width="10"
            height="13"
            src="../../assets/iamges/rankjiangpai.png"
            style="margin-right: 5px"
            alt=""
          />
          <span>{{ rankInfo.data.data[2].num }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { reactive, onMounted } from "vue";

const props = defineProps({
  data: Object,
});

const listRankTitle = reactive([
  { title: "分类排行榜", info: "所参与企业品牌分类排行", data: [] },
  { title: "地区排行榜", info: "所参与企业品牌所属地区排行", data: [] },
]);

const rankInfo = reactive({ data: {} });

const rankStatus = reactive({
  status: false,
});

const getRank = () => {
  let data = 0;
  setInterval(() => {
    data += 1;
    if (data > 1) {
      data = 0;
    }
    rankInfo.data = { ...listRankTitle[data] };
  }, 5000);
};

onMounted(() => {
  for (let i = 0; i < props.data.category.length; i++) {
    listRankTitle[0].data.push({
      name: props.data.category[i].category_name,
      num: props.data.category[i].total_comments,
    });
  }
  for (let i = 0; i < props.data.area.length; i++) {
    listRankTitle[1].data.push({
      name: props.data.area[i].region_name,
      num: props.data.area[i].total_comments,
    });
  }
  rankStatus.status = true;
  rankInfo.data = { ...listRankTitle[0] };
  getRank();
});
</script>

<style scoped lang="scss">
#list_rank {
  width: calc(100%);
  height: 104px;
  max-width: 500px;
  display: flex;
}
.text_over {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.label_info {
  margin-top: 10px;
  line-height: 1.5rem;
  padding: 0px 5px;
  background-image: -webkit-linear-gradient(
    left,
    transparent,
    rgba(255, 255, 255, 0.16) 15%,
    rgba(255, 255, 255, 0.16) 85%,
    transparent
  );
  text-align: center;
}
#rank_trophy {
  position: relative;
  width: 100%;
  background: url("../../assets/iamges/jiangpai.png");
  padding-bottom: 71%;
  background-size: 100%;
}
.rank_trophy_four {
  color: white;
  text-align: center;
  position: absolute;
  width: 28%;
  height: 15%;
  bottom: 10%;
  left: 2%;
}
.rank_trophy_five {
  color: white;
  text-align: center;
  position: absolute;
  width: 32%;
  height: 15%;
  bottom: 18%;
  left: 34%;
}
.rank_trophy_six {
  color: white;
  text-align: center;
  position: absolute;
  width: 29%;
  height: 15%;
  bottom: 10%;
  left: 69%;
}
</style>
