<template>
  <div id="home_page">
    <div id="home_pahe_head">
      <div class="use_people">
        昨日有<span style="margin: 0px 10px; color: #f9d8a2"
          >{{ 100000 + Math.floor(Math.random() * 100000) }}用户</span
        >检索了品牌行业
      </div>
      <div id="home_search">
        <input
          type="text"
          ref="searchValue"
          placeholder="搜行业、搜品牌、搜企业"
        />
        <div class="search_input">
          <div id="home_search_btn" @click="getSearch">搜索</div>
        </div>
      </div>
      <div style="height: 10px"></div>
      <ListRank :data="rankList" v-if="currentStatus.rankStatus" />
    </div>
    <div style="max-width: 500px; margin: auto" v-if="!currentStatus.status">
      <TitleLabel :value="`分类排行榜`" />
      <div style="padding: 10px">
        <RankListOne :data="categoryList" v-if="currentStatus.categoryStatus" />
      </div>
      <TitleLabel :value="`地区排行榜`" />
      <div style="padding: 10px">
        <RankListTwo :data="areaList" v-if="currentStatus.areaStatus" />
      </div>
    </div>
    <div style="max-width: 500px; margin: auto" v-else>
      <SearchList :data="currentStatus.data" />
    </div>
  </div>
</template>

<script setup>
import { ref, reactive } from "vue";
import ListRank from "./compenmens/ListRank.vue";
import TitleLabel from "./compenmens/TitleLabel.vue";
import RankListOne from "./compenmens/RankListOne.vue";
import RankListTwo from "./compenmens/RankListTwo.vue";
import SearchList from "./compenmens/SearchList.vue";
import { fether } from "@/utils/tools";

const searchValue = ref("");

const currentStatus = reactive({
  status: false,
  data: {
    value: [],
    name: "",
    page_count: 0,
  },
  categoryStatus: false,
  areaStatus: false,
  rankStatus: false,
});

const categoryList = reactive([]);
const areaList = reactive([]);
const rankList = reactive({
  category: [],
  area: [],
});

const getSearch = async () => {
  if (searchValue.value.value === "") {
    currentStatus.status = false;
    return;
  }
  let result = await fether(
    `/searchenterprise?search_value=${searchValue.value.value}`
  );
  if (result.code === 200) {
    result.data.map((item) => {
      currentStatus.data.value.push({
        ...item,
      });
    });
    currentStatus.data.name = searchValue.value.value;
    currentStatus.data.page_count = result.page_count;
    currentStatus.status = true;
    searchValue.value = "";
  }
};

// 截取数据
const getRankList = () => {
  let arr = categoryList.slice(0, 3);
  let Arr = areaList.slice(0, 3);
  (rankList.category = [...arr]), (rankList.area = [...Arr]);
  currentStatus.rankStatus = true;
};

// 获取分类排行榜
const getCategoryList = async () => {
  let result = await fether("/categoryleaderboard");
  let result1 = await fether("/regionleaderboard");
  if (result.code === 200) {
    result.data.map((item, index) => {
      categoryList.push({
        ...item,
        rank: index + 1,
      });
    });
    categoryList.sort((a, b) => {
      return b.total_comments - a.total_comments;
    });
    currentStatus.categoryStatus = true;
  }
  if (result1.code === 200) {
    result1.data.map((item) => {
      areaList.push(item);
    });
    areaList.sort((a, b) => {
      return b.total_comments - a.total_comments;
    });
    currentStatus.areaStatus = true;
  }
  getRankList();
};
getCategoryList();
</script>

<style lang="scss" scoped>
#home_page {
  width: 100vw;
  max-width: 750px;
  height: 100vh;
  overflow-y: scroll;
  margin: auto;
}
#home_pahe_head {
  background-color: #c82e26;
  padding: 10px;
  margin: auto;
  max-width: 500px;
}
.use_people {
  text-align: center;
  font-size: 15px;
  color: white;
}
#home_search {
  height: 30px;
  border-radius: 25px;
  display: flex;
  background-color: white;
  margin-top: 10px;
}
.search_input {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
input {
  flex: 1;
  outline: none;
  border: none;
  border-radius: 25px 0px 0px 25px;
  padding-left: 15px;
}
#home_search_btn {
  width: 40px;
  height: 20px;
  border-radius: 10px;
  background-color: #fcdad1;
  color: #cc3d44;
  font-size: 12px;
  text-align: center;
  line-height: 20px;
}
#label_list {
  display: flex;
  font-size: 12px;
  color: white;
  margin: 10px auto;
  height: 20px;
  overflow-y: scroll;
  flex-wrap: wrap;
}
#home_page::-webkit-scrollbar,
#label_list::-webkit-scrollbar {
  display: none;
}
</style>
