<template>
  <div style="display: flex; flex-wrap: wrap; max-width: 500px; height: 30px">
    <div
      v-for="(item, index) in pageList"
      :key="item"
      class="page_item"
      :style="{
        border: item.status ? '1px solid #409EFF' : '1px solid #666',
        color: item.status ? '#409EFF' : '#666',
      }"
      @click="surePage(item.value, index)"
    >
      {{ item.value }}
    </div>
  </div>
</template>

<script setup>
import { reactive, watch } from "vue";
import { useStore } from "vuex";
const $store = new useStore();
const props = defineProps({
  pageCont: Number,
  type: String,
});

const pageList = reactive([]);

const pageStatus = reactive({
  data: 0,
  key: 0,
});
const getList = (newVal, data) => {
  if (pageList.length) {
    pageList.splice(0, pageList.length);
  }
  if (!data) {
    data = 0;
  }
  if (newVal % 10 === 0) {
    pageStatus.key = newVal / 10;
  } else {
    pageStatus.key = Math.floor(newVal / 8);
  }
  if (newVal <= 10) {
    for (let i = 0; i < newVal; i++) {
      pageList.push({
        value: i + 1,
        status: i + 1 === 1 ? true : false,
      });
    }
  } else {
    if (data < pageStatus.key - 1) {
      for (let i = 0; i < 10; i++) {
        if (i === 9) {
          pageList.push({
            value: newVal,
            status: false,
          });
        } else if (i === 8) {
          pageList.push({
            value: "...",
            status: false,
          });
        } else {
          pageList.push({
            value: i + 1 + data * 8,
            status: i + 1 + data * 8 === 1 ? true : false,
          });
        }
      }
    } else if (data === pageStatus.key - 1) {
      let Num = newVal - 8 * data;
      pageList.push({ value: "...", status: false });
      for (let i = 0; i < Num; i++) {
        pageList.push({ value: 8 * data + i + 1, status: false });
      }
    } else {
      pageStatus.data = 0;
      getList(newVal, pageStatus.data);
    }
  }
};

const surePage = (value, index) => {
  if (pageList.length === 1) return;
  if (value !== "...") {
    for (let i = 0; i < pageList.length; i++) {
      if (i === index) {
        pageList[i].status = true;
      } else {
        pageList[i].status = false;
      }
    }
    $store.state.updataPage = value;
  } else {
    pageStatus.data += 1;
    getList(25, pageStatus.data);
  }
};
watch(
  () => props.pageCont,
  (newVal) => {
    getList(newVal);
  }
);
</script>

<style scoped lang="scss">
.page_item {
  padding: 5px 10px;
  margin-right: 10px;
  margin-top: 10px;
}
</style>
